
.icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  vertical-align: top;
  fill: currentColor;
  stroke: currentColor;
}
.icon[data-title="logo"] {
    width: 118px;
    height: 86px;
    fill: #fff;
}
.icon[data-title="letter-a"] {
    width: 21px;
    height: 21px;
}
.icon[data-title="letter-b"] {
    width: 21px;
    height: 21px;
}
.icon[data-title="plus"] {
    width: 16px;
    height: 16px;
}
.icon[data-title="person"] {
    fill: #33D098;
}
.icon[data-title="people"] {
    opacity: .5;
}



